import { FC, memo } from 'react';
import { LabelProps } from '@thyme-ui/types';
import cn from 'classnames';

import { Typography } from '../Typography';

const REQUIRED_START = '*';
const rootClass = 'thuiCommonLabel';

export const Label: FC<LabelProps> = memo(
  ({ label, htmlFor, className, isRequired = false, disabled, color }: LabelProps) => {
    const labelClasses = cn(rootClass, className, { [`${rootClass}--disabled`]: disabled });

    return (
      <label htmlFor={htmlFor} className={labelClasses}>
        <Typography tag="span" color={color}>
          {label}
        </Typography>
        {isRequired && !disabled && <Typography tag="span">{REQUIRED_START}</Typography>}
      </label>
    );
  }
);
